  .middle-menu-btn {
    width:26px;
    height: 4px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 5px #534a4a;
    transition: all .5s ease-in-out;
  }
  .middle-menu-btn::before,
  .middle-menu-btn::after {
    content: '';
    position: absolute;
    width:40px;
    height: 4px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 5px #534a4a;
    transition: all .5s ease-in-out;
  }
  .middle-menu-btn::before {
    -webkit-transform: translateY(-13px);
            transform: translateY(-13px);
  }
  .middle-menu-btn::after {
    -webkit-transform: translateY(13px);
            transform: translateY(13px);
  }
  /* ANIMATION */
  .menu-btn.open .middle-menu-btn {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
    background: transparent;
    box-shadow: none;
  }
  .menu-btn.open .middle-menu-btn::before {
    -webkit-transform: rotate(45deg) translate(35px, -35px);
            transform: rotate(45deg) translate(35px, -35px);
  }
  .menu-btn.open .middle-menu-btn::after {
    -webkit-transform: rotate(-45deg) translate(35px, 35px);
            transform: rotate(-45deg) translate(35px, 35px);
  }

